import React, { useState } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import { Box } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import { Grid } from '@material-ui/core';
import IntlMessages from '../../../@jumbo/utils/IntlMessages';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import { Redirect, Route, Switch } from 'react-router';
import Typography from '@material-ui/core/Typography';
import { useLocation } from 'react-router-dom';
import { useEffect } from 'reactn';
import {Contracts, Formatter, Accounts} from '../../../utils/index';
import { get } from 'lodash';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';

const useStyles = makeStyles(theme => ({
    buttonRoot: {
        '& > *': {
          margin: theme.spacing(2),
        },
        textAlign: 'center'
      },
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      '& > *': {
        backgroundColor: lighten(theme.palette.background.paper, 0.1),
        backgroundimage: '/images/dashboard/Fund1.png',
        // margin: theme.spacing(2),
        // width: theme.spacing(35),
        // height: theme.spacing(25),
      },
    },
    padding: {
        padding: '10px',
        marginTop: '30px',
        width: '100%'
      },
    paddingWarning: {
        marginTop: '25px',
        marginBottom: '15px',
        marginTop: '25px',
        paddingTop: '1px',
        width: '100%',
    },
    tokenPadding: {
        marginBottom: '7px'
    },
    tokenPaddingBottom: {
        marginTop: '15px',
        marginBottom: '5px'
    },
    h1Variant: {
        fontSize: '45px',
        margin: '0 auto',
        marginTop: '8%',
        width: '80%',
        textAlign: 'center'
    },
    subHeader: {
        textAlign: 'center',
        margin: '0 auto',
        marginTop: '35px',
        marginBottom: '30px',
        fontSize: '17px',
        width: '85%'
    },
    subHeader1: {
        textAlign: 'center',
        margin: '0 auto',
        marginBottom: '30px',
        fontSize: '17px',
        width: '85%',
        display: 'block'
    },
    subHeader2: {
        textAlign: 'center',
        margin: '0 auto',
        marginTop: '15px',
        marginBottom: '10px',
        fontSize: '17px',
        width: '85%',
        display: 'block'
    },
    reason1: {
        textAlign: 'center',
        marginTop: '25px',
        marginBottom: '30px',
        fontSize: '25px'
    },
    viewPoolContract: {
        marginBottom: '5px',
        
    }
  }));

function detectNativeToken() {
    if (window.localStorage.getItem('chain') == 'ETH') {
        return ' ETH'
    } else {
        return ' BNB'
    }
}



export default function SimplePaper() {
  const classes = useStyles();
  const location = useLocation();
  const [poolInfo, setPoolInfo] = React.useState(null);
  const [alignment, setAlignment] = React.useState('left');

  const handleAlignment = (event, newAlignment) => {
    setAlignment(newAlignment);
    if(newAlignment != 'left') {
        window.localStorage.setItem('chain', 'BSC');
    } else {
        window.localStorage.setItem('chain', 'ETH');
    }
    window.location.reload();
  };

  useEffect(() => {
    async function loadData() {
      let url = location.pathname.split('/');
      let chain = window.localStorage.getItem('chain');
      if (chain == 'ETH') {
          setAlignment('left');
      } else {
          setAlignment('right');
      }
    }       

    loadData();
  }, []);

  return (
    <PageContainer>
        <div>
            <Typography className={classes.h1Variant} variant="h1" component="h2" gutterBottom>
               Decentralized Onchain Fund Management
            </Typography>
            <Typography className={classes.subHeader} variant="h3" gutterBottom>
                Join actively managed funds or create your own fund and become a pool manager.
            </Typography>
            
            <Box className={classes.buttonRoot}>
                <Button href='/dashboard/selfmanager' variant="contained" color="primary">
                    Create your own fund
                </Button>
                <Button href='https://stake.nyan.finance' variant="outlined">Stake</Button>
                <Button href='https://www.learnnyan.finance' variant="outlined">Documentation</Button>
            </Box>
            <Box className={classes.buttonRoot}>
                <Paper className={classes.paddingWarning} elevation={2}>
                        <Typography className={classes.reason1} variant="h1" gutterBottom>
                            What chain are you using?
                        </Typography>
                        <ToggleButtonGroup value={alignment} exclusive onChange={handleAlignment} aria-label="text alignment">
                            <ToggleButton value="left" aria-label="left aligned">
                                Ethereum 
                            </ToggleButton>
                            
                            <ToggleButton value="right" aria-label="centered">
                                Binance Smart Chain
                            </ToggleButton>
                        </ToggleButtonGroup>
                        {alignment == 'left' ?
                            <Typography className={classes.subHeader2} variant="p" gutterBottom>
                                Make sure to set your Metamask wallet to the Ethereum mainnet.
                            </Typography>
                            :
                            <Typography className={classes.subHeader2} variant="p" gutterBottom>
                                Make sure to set your Metamask wallet to the BSC mainnet.
                            </Typography>
                        }
                        
                    </Paper>
            </Box>
            <Box className={classes.root} >
                <Paper className={classes.paddingWarning} elevation={2}>
                    <Typography className={classes.reason1} variant="h1" gutterBottom>
                        Why create your own pool?
                    </Typography>
                    <img src="/images/dashboard/Fund1.png"></img>
                    <Typography className={classes.subHeader1} variant="h4" gutterBottom>
                        Most new entrants to crypto are unsure of what assets to acquire and hold. As a pool manager, 
                        you make money for simply telling a smart contract where to move liquidity to.
                    </Typography>
                </Paper>
            </Box>
        </div>
    </PageContainer>
    
  );
}
